import React, { useEffect, useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar,Doughnut } from "react-chartjs-2";
import { getProductsByIDs } from "../../../api/products";
import { BarsChartContainer } from "./styles";
import { useShoppingCartStore } from "../../../store/shoopingCartStore";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function formatProducts(products, setCegories) {
  const categories = [...new Set(products.map((product) => product.category))];
  setCegories(categories);
  return categories.reduce((acc, category) => {
    const _products = products.filter(
      (product) => product.category === category
    );
    return [...acc, { category: category, products: _products }];
  }, []);
}

const BarsChart = ({width , heigth }) => {
  const { sc } = useShoppingCartStore();

  const [products, setProducts] = useState([]);
  const [categories, setCegories] = useState([]);
  const [formated, setFormated] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const res = await getProductsByIDs(sc);
      console.log("res", res);

      setProducts(res);
    }
    fetchData();
  }, [sc]);

  useEffect(() => {
    const formated = formatProducts(products, setCegories);
    setFormated(formated);
  }, [products]);
  const data = {
    labels: categories.map((cat) => cat),
    datasets: [
      {
        label: "Products",
        data: formated.map((cat) => cat.products.length),
        borderColor: ["rgba(255, 206, 86, 0.2)"],
        backgroundColor: formated.map(
          (cat) => `#${Math.floor(Math.random() * 16777215).toString(16)}`
        ),
      },
    ],
  };
  const options = {
    title: {
      display: true,
      text: "Your Shopping Cart Products By Categories",
    },

  };
  return (
    <>
      {/* <h1>Your Shopping Cart Products By Categories</h1> */}
      <BarsChartContainer>
        <Bar data={data} options={options} />
        {/* <Doughnut data={data} options={options} /> */}
      </BarsChartContainer>
    </>
  );
};

export default BarsChart;
